@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 640px) {
  body {
    max-width: 830px !important;
    font-size: small !important;
  }
}

.nav {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
  height: auto;
  z-index: 1;
  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav_logo {
  position: fixed;
  left: 5%;
  width: 7%;
  object-fit: contain;
}

@media only screen and (max-width: 640px) {
  .nav_logo {
    width: 15%;
  }
}

.banner_contents {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.banner_buttons {
  font-size: 20px;
}

.container {
  position: relative;
  text-align: center;
  color: white;
}

.banner_title {
  color: black;
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

@media only screen and (max-width: 640px) {
  .banner_title {
    font-size: 32px;
    font-weight: 800;
  }
  .banner_buttons {
    font-size: 32px;
  }
}

.form {
  padding-left: 20%;
  padding-right: 20%;
  padding-top: 20px;
  text-align: left;
}

.schIcons {
  width: 50%;
}

.ant-steps-item-process .ant-steps-item-icon {
  background: #c85366 !important;
  border: none;
}

@media only screen and (max-width: 640px) {
  p {
    font-size: 14px;
  }
  h2 {
    font-size: 18px;
  }
  h1 {
    font-size: 24px;
  }
  .schIcons {
    width: 100%;
  }
}

.footer {
  padding-left: 10%;
  padding-right: 10%;
}

.footer-content {
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: left;
}

@media only screen and (max-width: 640px) {
  .footer {
    padding-left: 0%;
    padding-right: 0%;
  }
  .footer-content {
    padding-left: 2%;
    padding-right: 2%;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

